* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

#html {
  word-break: break-all;
}

#div1 {
  position: fixed;
  left: 50%;
  transform: translateX(-50%);
  top: 20px;
}

#div1::before {
  content: "";
  display: block;
  position: absolute;
}

#div1::after {
  content: "";
  display: block;
  position: absolute;
}

@media (max-width: 500px) {
  #html {
    height: 65vh;
    overflow: auto;
  }

  #divWrapper {
    height: 35vh;
  }

  #div1 {
    position: relative;
    top: 0;
    right: 0;
    left: 50%;
    transform: translateX(-50%);
  }
}
